import { h, Component, createRef } from 'preact';
import { observer } from 'mobx-preact';

import {
	Circle,
	FeatureGroup,
	LayerGroup,
	LayersControl,
	Map,
	Marker,
	Popup,
	Rectangle,
	TileLayer,
	VideoOverlay,
	ImageOverlay,
	GeoJSON,
	WMSTileLayer,
} from 'react-leaflet'

const { BaseLayer, Overlay } = LayersControl

import icons from '../../lib/icons';
import util from '../../lib/util';

import '../../../node_modules/leaflet/dist/leaflet.css';
import '../../css/toggle-bootstrap.min.css';
import '../../css/toggle-bootstrap-dark.min.css';
import '../../css/custom-bootstrap-dark.css';
import '../../fontawesome-free-5.8.1-web/css/all.css';

// import geojsonData from '../../assets/295.json';

import L from "leaflet";

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
    iconRetinaUrl: require('../../../node_modules/leaflet/dist/images/marker-icon-2x.png'),
    iconUrl: require('../../../node_modules/leaflet/dist/images/marker-icon.png'),
    shadowUrl: require('../../../node_modules/leaflet/dist/images/marker-shadow.png')
});

const MAX_BOUNDS = [
	[52.059295, 15.067042],
	[52.040691, 15.139619],
];

const zoom = 16;
const position = [52.051959,15.085487];
const center = position;
const rectangle = [
	[52.051959, 15.085487],
	[52.061959, 15.095487],
];

const circle2 = [52.061959, 15.085487]

import style from './style';

@observer
class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			hasLocation: false,
		};
		const { waypointStore } = this.props.stores;
		waypointStore.load();
	}

	mapRef = createRef();

	onViewportChanged = (e) => {
		console.log('onViewportChanged', e);
	}


	// handleClick = () => {
	// 	const map = this.mapRef.current
	//     if (map != null) {
	// 		map.leafletElement.locate()
	//     }
	// }

	handleLocationFound = (e) => {
		this.setState({
			hasLocation: true,
			latlng: e.latlng,
	    })
	}

	geoJSONStyle() {
		return {
			color: '#1f2021',
			weight: 1,
			fillOpacity: 0.5,
			fillColor: '#fff2af',
		}
	}

	onEachFeature(feature, layer) {
		const popupContent = ` <Popup><p>Customizable Popups <br />with feature information.</p><pre>Borough: <br />${feature.properties.name} ${JSON.stringify(feature)}</pre></Popup>`
		layer.bindPopup(popupContent)
	}

	handleClick = (e) => {
		const { appState } = this.props.stores;
		appState.setLatlng(e.latlng);
		// this.setState({ currentPos: e.latlng });
	}

	componentDidMount() {
		util.toggleDarkModeClasses(true);
	}

	render() {
		const { appState, waypointStore } = this.props.stores;
		const { latlng } = appState;
		const { waypoints } = waypointStore;

		return (
			<div class={`${style.home}`} style='height: 100%;'>
				<Map
					minZoom={6}
					// maxBounds={MAX_BOUNDS}
					center={position}
					onClick={this.handleClick}
			        onLocationfound={this.handleLocationFound}
					// ref={(r) => { this.mapRef = r; }}
					ref={this.mapRef}
					zoom={zoom}

					zoomControl={true}
	                doubleClickZoom={true}
					scrollWheelZoom={true}
					dragging={true}
					animate={true}
					easeLinearity={0.35}

					style='width: 100%; min-height: 500px; height: 100%;' className='h-100'
				>
					<LayersControl position="topright">
						<BaseLayer checked name="OpenStreetMap.Mapnik">
							<TileLayer
								attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
								url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
							/>
						</BaseLayer>
						<BaseLayer name="OpenStreetMap.BlackAndWhite">
							<TileLayer
								attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
								url="https://tiles.wmflabs.org/bw-mapnik/{z}/{x}/{y}.png"
							/>
						</BaseLayer>
						<BaseLayer name="OpenStreetMap.Carto">
							<TileLayer
								attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, &copy; <a href="https://carto.com/attribution">CARTO</a>'
								url="https://{s}.basemaps.cartocdn.com/light_nolabels/{z}/{x}/{y}.png"
							/>
						</BaseLayer>
						<BaseLayer name="OpenStreetMap.Hikemap">
							<TileLayer
								attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, &copy; wmflabs Hike & Bike'
								url="https://tiles.wmflabs.org/hikebike/{z}/{x}/{y}.png"
							/>
						</BaseLayer>
						<BaseLayer name="OpenStreetMap.Dark">
							<TileLayer
								attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, &copy; Map tiles by Carto, under CC BY 3.0. Data by OpenStreetMap, under ODbL.'
								url="https://cartodb-basemaps-{s}.global.ssl.fastly.net/dark_all/{z}/{x}/{y}.png"
							/>
						</BaseLayer>
						<BaseLayer name="OpenStreetMap.Hiking">
							<TileLayer
								attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, &copy; Map tiles by Carto, under CC BY 3.0. Data by OpenStreetMap, under ODbL.'
								url=" http://{s}.tiles.wmflabs.org/hikebike/{z}/{x}/{y}.png"
							/>
						</BaseLayer>
						<Overlay name="Marker with popup">
							<Marker position={center} icon={icons.get('buddhism')}>
								<Popup>
									A pretty CSS3 popup. <br /> Easily customizable.
								</Popup>
							</Marker>
						</Overlay>
						<Overlay checked name="Layer group with circles">
							<LayerGroup>
								<Circle center={center} fillColor="blue" radius={200} />
								<Circle
									center={center}
									fillColor="red"
									radius={100}
									stroke={false}
								/>
								<LayerGroup>
									<Circle
										center={circle2}
										color="green"
										fillColor="green"
										radius={100}
									/>
								</LayerGroup>
							</LayerGroup>
						</Overlay>
						<Overlay name="Feature group">
							<FeatureGroup color="purple">
								<Popup>Popup in FeatureGroup</Popup>
								<Circle center={circle2} radius={200} />
								<Rectangle bounds={rectangle} />
							</FeatureGroup>
						</Overlay>

						<Overlay name="Geoportal.gov.pl">
							<WMSTileLayer
								layers={'Raster'}
								format={'image/png'}
								transparent={true}
								url="http://mapy.geoportal.gov.pl/wss/service/img/guest/ORTO/MapServer/WMSServer?"
							/>
						</Overlay>
						<Overlay name="Wildfire">
							<WMSTileLayer
								layers={'GWIS'}
								format={'image/png'}
								transparent={true}
								url={'https://ies-ows.jrc.ec.europa.eu/gwis?language=eng&'}
								attribution={'European Commission, Joint Research Centre'}
							/>
						</Overlay>
						
					</LayersControl>
					<ImageOverlay
						url='./assets/frame.png'
						bounds={[
							[52.056765505175235, 15.068457126617433],
							[52.04421634105194, 15.105557441711428],
						]}
					/>


					{/* <GeoJSON
						data={geojsonData}
						style={this.geoJSONStyle}
						onEachFeature={this.onEachFeature}
					/> */}

					{latlng && <Marker position={latlng} draggable={true} icon={icons.get('marker')}>
						<Popup position={latlng}>
							Current location: <pre>{JSON.stringify(latlng, null, 2)}</pre>
							<a href='/profile'>Add new location</a>
						</Popup>
					</Marker>}

					{waypoints.length > 0 && waypoints.map(wp => (
						<Marker position={[wp.lat, wp.lng]} draggable={false} icon={icons.get(wp.icon || 'marker')}>
							<Popup position={[wp.lat, wp.lng]}>
								Ping location: <pre>{JSON.stringify(wp, null, 2)}</pre>
							</Popup>
						</Marker>
					))}

				</Map>
			</div>
		);
	}
}

export default App;
